<!--选择设备-->
<template>

  <van-popup
      v-model:show="show"
      style="height: 200px;"
      position="bottom"
      closeable
  >
    <div>
      <span class="add-data-title" >添加数据</span>
      <div class="add-data" >
        <div class="add-data-item"  @click="onAddRecord">
          <div style="background: rgb(41, 111, 197)" >
            <img :src="require('@/assets/Input.png')" />
          </div>
          <span>手动录入</span>
        </div>
        <div class="add-data-item" @click="onBindDevice">
          <div style="background: rgb(255, 189, 99)" >
            <img :src="require('@/assets/binding.png')" />
          </div>
          <span>绑定设备</span>
        </div>
        <div v-show="fromPage === ''" class="add-data-item">
          <div>
           <img src="@/assets/addnaolu.png" alt="" class="brain">
          </div>
          <span>绑定脑路科技</span>
        </div>
      </div>
    </div>
    </van-popup>

    <div class="div-change-device-content" :style="{backgroundColor:backgroundColor}">
        <div @click="onShow" :style="{backgroundColor:backgroundColor}">
            <img style="height: 14px;width: 14px" :src="backgroundColor?require('@/assets/add_white.png'):require('@/assets/add.png')">
            <span :style="{ marginLeft: '3px', color:fontColor ? fontColor: '#FFFFFF' }">添加数据</span>
        </div>
        <div @click="onChoiceDevice" :style="{ backgroundColor: backgroundColor}" >
            <span :style="{ color: fontColor ? fontColor: '#FFFFFF'}">全部</span>
            <img :src="backgroundColor?require('@/assets/drop_down_white.png'):require('@/assets/arrow_lower.png')" style="margin-left: 2px"/>
        </div>
        <div @click="onGetData">
            <img :src="backgroundColor?require('@/assets/update_white.png'):require('@/assets/update.png')">
            <span :style="{marginLeft: '3px',color: fontColor ? fontColor: '#FFFFFF'}">获取数据</span>
        </div>
    </div>

  <!--点击手动录入弹框-->
  <van-popup v-model:show="showModal" :style="{width:'40%',borderRadius:'5px'}">
    <div class="list-row">
      <p @click="onAddManualRecord">
        <img src="@/assets/ic_night.png" alt="" class="img-night">
        <span>夜间睡眠记录</span>
      </p>
    </div>
    <div class="list-row">
      <p @click="onAddManualRecord">
        <img src="@/assets/ic_sun.png" alt="" class="img-lxxs">
        <span>零星小睡记录</span>
      </p>
    </div>
  </van-popup>

  <van-popup v-model:show="showDevice" safe-area-inset-bottom position="bottom" :style="{ height: '20%' }">
    <div>
      <p class="title-row">
        全部
      </p>
      <div class="div-cancel" @click="onCancel">
        <p class="p-cancel">
          取消
        </p>
      </div>
    </div>
  </van-popup>
</template>
<script>
import {reactive, onMounted, toRefs} from 'vue'
import {useRouter} from "vue-router";

export default {
  name: "ChangeDevice",
  props: {
    fromPage:{
      type: String,
      default:''
    },
    backgroundColor: {//设置背景颜色并，更改字体和图片
      type: String,
      default: ''
    },
    fontColor:{
      type: String,
      default: ''
    },
    device: {
      type: String,
      default: '',
    },
    otherAddClick: Function
  },
  components: {},
  setup(props) {
    const router = useRouter()
    onMounted(
        () => {
          console.log('fromPage',props.fromPage)
        }
    )
    const onBindDevice = () => {
      router.push('/device-list')
    }
    const goTo = (r, query) => {
      router.push({path: r, query: query || {}})
    }
    // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
    const state = reactive({
      show: false,
      showModal: false,
      showDevice: false,
    })

    const onShow = () => {
      state.show = true
    }
    const onAddRecord = () => {
      if(props.fromPage === "bloodpressure" || props.fromPage === "motion" ||
          props.fromPage === "heartRate" ||props.fromPage === "bloodSugar" || props.fromPage === "weight"){ // 血压
        props.otherAddClick()
        console.log("33333333")
      }else{
        state.show = false;
        state.showModal = true;
      }
    }
    const onChoiceDevice = () => {
      state.showDevice = true;
    }
    const onAddManualRecord = () => {
      goTo('/manual-record')
    }
    const onGetData = () => {
    }
    const onCancel = ()=>{
      state.showDevice = false;
    }
    // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
    return {
      ...toRefs(state),
      onShow,
      onChoiceDevice,
      onAddManualRecord,
      onGetData,
      onAddRecord,
      onCancel,
      onBindDevice
    }
  }
}
</script>
